import React, { useEffect, useMemo, useState } from "react";
import { Route, Routes as AllRoutes } from "react-router-dom";
import UserMainPage from "./pages/users";
import { ROLE } from "./helpers/constants"
import { useDispatch, useSelector } from "react-redux";
import { REDUCER_TYPES } from "./store";
import { resetAuth } from "./store/api/utils";
import Error from "./pages/error";
import GroupsPage from './pages/groups'
import OgProfile from './pages/og_profile';
import Test from "./pages/test"
import TestUpload from "./pages/uploadTest";
import SkillPage from "./pages/skills";
import QuestionBank from "./pages/questionBank";
import SkillMatrix from "./pages/skillMatrix"
import TestPortal from "./pages/testPortal"
import TrainingPage from "./pages/planTraining"
import TestDetail from "./pages/test/testDetail"
import AddReport from "./pages/report"
import Op_ChangePassword from './pages/Profile'
import ChangeLanguage from './pages/ChangeLanguage'
import TestUserPage from "./pages/TestUsers";
import ResultPage from "./pages/ResultPage";
import CardGrid from "./pages/machineMonitoring";
import ChildMachine from './pages/machineMonitoring/childMachine'

const Routes = () => {
//   const localMenu = localStorage.getItem('menu')
//   const menuParse = JSON.parse(localMenu);
//   const menuSet = new Set(menuParse);
// console.log("localMenu--------",menuSet)

  const dispatch = useDispatch();
  const [hydrated, setHydrated] = useState(false);
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const role = state?.user?.data?.role
  const [menuSet, setMenuSet] = useState(new Set());
  
  useEffect(() => {
    const localMenu = localStorage.getItem('menu');
    if (localMenu) {
      const menuParse = JSON.parse(localMenu);
      setMenuSet(new Set(menuParse)); // Set the menuSet with the value from localStorage
    }
    resetAuth(dispatch, state);
    setHydrated(true);
  }, [dispatch, state]);
     

  const routes = [
    {
      name:'test result',
      path: '/test-users',
      element: <TestUserPage />
    },
    {
      name:"performance",
      path: '/report',
      element: <AddReport />
    },
    {
      name:"plan training",
      path: '/plan-training',
      element: <TrainingPage/>
    },
    {
      name:"test portal",
      path: '/test-portal',
      element: <TestPortal />
    },
    {
      name:"skill matrix",
      path: '/skill-matrix',
      element: <SkillMatrix />
    },
    {
      name:"test master",
      path: '/test-upload',
      element: <TestUpload />
    },
    {
      name:"skill page",
      path: '/skill-page',
      element: <SkillPage />
    },
    {
      name:"question bank",
      path: '/question-bank',
      element: <QuestionBank />
    },
    {
      name:"groups",
      path: '/group',
      element: <GroupsPage />
    },
    {
      name:"users",
      path: "/users",
      element:  <UserMainPage /> ,
    },
    {
      name:"test portal",
      path: '/test',
      element: <Test />
    },
    {
      name:"test portal",
      path: '/TestDetail',
      element: <TestDetail />
    },
    {
      name:'test result',
      path: '/result-page',
      element: <ResultPage />
    },
    {
      path: '/error',
      element: <Error />
    }, 
   
  ];

  const alwaysAccessibleRoutes = [
    { path: "/profile", element: <OgProfile /> },
    { path: '/change-password', element: <Op_ChangePassword /> },
    { path: '/change-language', element: <ChangeLanguage /> },
    { path: '/machine-monitoring', element: <CardGrid /> },
    { path: '/child-machine', element: <ChildMachine /> },
  ];

  // const modifiedRoutes = useMemo(() => {
  //   return routes.map(route => {
  //     // Check if route's name exists in the static menuSet
  //     if (!menuSet.has(route.name)) {
  //       return { ...route, element: <Error /> };  // Return Error page if name not found in menu
  //     }
  //     return route;
  //   });
  // }, [routes]);

  const modifiedRoutes = routes.map(route => {
    if (!menuSet.has(route.name)) {
      return { ...route, element: <Error /> }; // Return Error page if name not found in menuSet
    }
    return route;
  });
  
  // useEffect(() => {
  //   resetAuth(dispatch, state);
  //   setHydrated(true);
  // }, []);

  if (!hydrated) {
    return null;
  }
  return (
    <AllRoutes>
      {modifiedRoutes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
      {alwaysAccessibleRoutes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
    </AllRoutes>

  );
};

export default Routes;
