import {
  Box,
  Drawer as MuiDrawer,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Icon,
  AppBar,
  Toolbar,
  Avatar,
  Stack,
  Menu,
  MenuItem,
  Button,
  Typography
} from "@mui/material";
import React, { useEffect, useState } from "react";
import Divider from '@mui/material/Divider';
import { useSelector } from "react-redux";
import { useTranslation } from 'react-i18next';
import {
  ChevronLeft,
  ChevronRight,
  Construction,
  Home,
  Logout,
  PeopleOutline,
  Settings,
  Widgets,
  ShoppingCartOutlined,
  email,
} from "@mui/icons-material";
import EmailIcon from '@mui/icons-material/Email';
import InventoryIcon from '@mui/icons-material/Inventory';
import PrecisionManufacturingIcon from '@mui/icons-material/PrecisionManufacturing';
import TimelineIcon from '@mui/icons-material/Timeline';
import HourglassBottomIcon from '@mui/icons-material/HourglassBottom';
import LogoutIcon from '@mui/icons-material/Logout';
import styled from "@emotion/styled";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import Routes from "../../routes";
import useAuth from '../../store/auth'
import { REDUCER_TYPES } from "../../store";
import { storeUser } from "../../store/api/utils";
import { COLORS, ROLE } from '../../helpers/constants';
import ModelTrainingIcon from '@mui/icons-material/ModelTraining';
import SwipeLeftIcon from '@mui/icons-material/SwipeLeft';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import StoreIcon from '@mui/icons-material/Store';
import PersonIcon from '@mui/icons-material/Person';
import PasswordIcon from '@mui/icons-material/Password';
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import SmartUdyog from '../../assets/png/dark-logo.png'
import Dhoot from '../../assets/png/dhoot.png'
import TranslateIcon from '@mui/icons-material/Translate';

const drawerWidth = parseInt(localStorage.getItem('draweWidth'));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    width: drawerWidth,
    // marginLeft:10,
    overflowX: "hidden",
    "& .MuiDrawer-paper": {
      width: drawerWidth,
      overflowX: "hidden",
      
    backgroundColor: COLORS.BG_GRAY,
    },
  }),
  ...(!open && {
    overflowX: "hidden",
    width: "50px",
    "& .MuiDrawer-paper": {
      overflowX: "hidden",
      width: "50px",
    },
  }),
}));

const NavigationLayout = () => {
  const t_token = localStorage.getItem("t_token")
  const { t, i18n } = useTranslation();
  const [menu_items, setMenu_items] = useState([])
  const isLoggedIn = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const user = useSelector((state) => state[REDUCER_TYPES.AUTH].user?.data)
  const { logout, profile2 } = useAuth()
  const [open, setOpen] = useState(true);
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const toggle = () => setOpen((val) => !val);
  const [anchorEl, setAnchorEl] = useState(null);
  const openpop = Boolean(anchorEl);

  const menu = [
    {
      name:"users",
      title: t('users') ,
      icon: PeopleOutline,
      path: "/users",
    },
    {
      name:"groups",
      title: t('groups') ,
      icon: Construction,
      path: "/group",
    },
    {
      name:"skill matrix",
      title:  t('skillMatrix'),
      icon: Widgets,
      path: "/skill-matrix",
    },
    {
      name:"plan training",
      title: t('planTraining'),
      icon: Widgets,
      path: "/plan-training",
    },
    {
      name:"skill page",
      title: t('skillPage'),
      icon: Widgets,
      path: "/skill-page",
    },
    {
      name:"performance",
      title:t('performance'),
      icon: Widgets,
      path: "/report",
    },
    {
      name:"test portal",
      title: t('testPortal'),
      icon: Widgets,
      path: "/test-portal",
    },
    {
      name:"question bank",
      title: t("questionBank"),
      icon: Widgets,
      path: "/question-bank",
    },
    {
      name:"test master",
      title: t('testMaster'),
      icon: Widgets,
      path: "/test-upload",
    },
    {
      name:'test result',
      title: t('testResult'),
      icon: Widgets,
      path: "/test-users",
    },
  ]

    // const profile = {
  //   title: t('profile') ,
  //   icon: PersonIcon,
  //   path: "/profile"
  // }

// "overview"

// "machine monitoring"


  useEffect(() => {
    // Check if the token exists
    if (!t_token) {
      toast.error("Please login!")
      navigate('/');
    }else{
      const menuList = localStorage.getItem("menu");
    const meuListParse = JSON.parse(menuList);
    console.log("navigation menu item ---->>",meuListParse)
    if(meuListParse!== null){
    const matchedMenu = menu.filter(item =>
      meuListParse.some(listItem => listItem === item.name)
    );
    setMenu_items(matchedMenu);
    }  
  }
  }, [t_token, navigate]);

  const handleClick = (event) => {
    console.log("popup", event.currentTarget)
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      {/* {isLoggedIn &&( */}
      <AppBar variant="outlined" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1, width: '100%', position: 'fixed', backgroundColor: COLORS.WHITE }}>
        <Stack sx={{ flexDirection: 'row', px: 1, py: 0.5, justifyContent: 'space-between' }}>
          <Stack sx={{ flexDirection: 'row', gap: 2, alignItems: 'center' }}>
            <img src={SmartUdyog} width='80px' height='43px' style={{ objectFit: 'contain' }} />
            <img src={Dhoot} width='100px' height='33px' style={{ objectFit: 'contain' }} />
          </Stack>
          {drawerWidth !==0 &&(
            <Stack sx={{ flexDirection: 'row', gap: 2, justifyContent: 'flex-end', alignItems: 'center' }}>
            <Typography className="fw-14" sx={{ width: '100%', pr: 2, mt: 1, color: COLORS.TEXT_GRAY }}>
              {t('welcome')} {user?.username}
            </Typography>
            {/* <Stack>
              <IconButton>
                <NotificationsNoneOutlinedIcon fontSize="large" />
              </IconButton>
            </Stack> */}
            <Toolbar
              sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}
              disableGutters
              className="h-50 minh-50"
            >
              <Stack
                direction="row"
                alignItems="center"
                justifyContent="space-between"
              // className="w-full "
              >
                {/* <Button
                    id="basic-button"
                    aria-controls={openpop ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={openpop ? 'true' : undefined}
                    onClick={handleClick}
                  > */}
                <IconButton
                  id="basic-button"
                  aria-controls={openpop ? 'basic-menu' : undefined}
                  aria-haspopup="true"
                  aria-expanded={openpop ? 'true' : undefined}
                  onClick={handleClick}
                >
                  <AccountCircleOutlinedIcon fontSize="large" />
                </IconButton>
                {/* </Button> */}
                <Menu
                  id="basic-menu"
                  anchorEl={anchorEl}
                  open={openpop}
                  onClose={handleClose}
                  MenuListProps={{
                    'aria-labelledby': 'basic-button',
                  }}
                >
                  {/* <MenuItem onClick={handleClose}>Profile</MenuItem>
                    <MenuItem onClick={handleClose}>My account</MenuItem> */}
                  <MenuItem sx={{ py: 0 }} onClick={() => { handleClose() }}>
                    <ListItemIcon>
                      <PersonIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.username}</ListItemText>
                  </MenuItem>
                  <Divider />
                  <MenuItem sx={{ py: 0 }} onClick={() => { handleClose() }}>
                    <ListItemIcon>
                      <EmailIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.email}</ListItemText>
                  </MenuItem>
                  <Divider />
                  {/* <MenuItem sx={{ py: 0 }} onClick={() => { handleClose() }}>
                    <ListItemIcon>
                      <PersonIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.role}</ListItemText>
                  </MenuItem>
                  <Divider /> */}
                  <MenuItem sx={{ py: 0 }} onClick={() => { handleClose(); navigate('/change-password') }}>
                    <ListItemIcon>
                      <PasswordIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{t('changePassword')}</ListItemText>
                  </MenuItem>
                  <Divider />
                  <MenuItem sx={{ py: 0 }} onClick={() => { handleClose(); navigate('/change-language') }}>
                    <ListItemIcon>
                      <TranslateIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{t('changeLanguage')}</ListItemText>
                  </MenuItem>
                  <Divider />
                  <MenuItem sx={{ py: 0 }} onClick={() => { handleClose(); logout() }}>
                    <ListItemIcon>
                      <Logout fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{t('logout')}</ListItemText>
                  </MenuItem>
                  {/* <Divider /> */}
                </Menu>
              </Stack>
            </Toolbar>
          </Stack>)}
        </Stack>
      </AppBar>
      <Drawer open={open} variant="permanent" onClose={toggle} sx={{}}>
        {/* <Box sx={{ width: drawerWidth,mt:8,backgroundColor:COLORS.BG_GRAY}} role="presentation">
          <h3 className={`${!open ? "display-none" : "ml-2"}`}>TRACEABILITY</h3>
          <IconButton onClick={toggle} sx={{ml:0.5}}>
            {open ? <ChevronRight /> : <ChevronLeft />}
          </IconButton>
        </Box> */}
        <Box sx={{ width: drawerWidth, backgroundColor: COLORS.BG_GRAY, mt: 7 }} role="presentation">
          <List dense>
            {menu_items.map(({ title, path, icon }) => (
              <ListItem
                key={path}
                sx={{ ml: 1 }}
                // className={`${pathname === path && "bg-opaque"}`}
                button
                onClick={() => navigate(path)}
              >
                {/* <ListItemIcon className="minw-40">
                  <Icon
                    component={icon}
                    className={`${
                      pathname === path ? "fw-700" : "fw-700"
                    }`}
                  />
                </ListItemIcon> */}
                <ListItemText sx={{
                  '& .MuiListItemText-primary': {
                    fontSize: '16px', // Change font size
                    fontWeight: `${pathname === path ? '700' : '500'}`
                  },
                }}

                  primary={title}
                  className={`${pathname === path ? "fw-700" : "fw-700"}`}
                />
              </ListItem>
            ))}
            {/* <ListItem>
              <ListItemIcon>
                <Home/>
                <ListItemText primary='submenu'/>
              </ListItemIcon>
            </ListItem> */}
          </List>
        </Box>
      </Drawer>
      <Box
        sx={{
          mt: 8,
          position: "relative",
          left: open ? drawerWidth : "50px",
          overflow: "hidden",
        }}
      >
        <Box sx={{ maxWidth: `calc(100vw - ${open ? drawerWidth : 50}px)` }}>
          <Routes />
        </Box>
      </Box>


      {/* )} */}
    </>

  )
};

export default NavigationLayout;
